<template>
	<div class="body">
		<div class="company_name flex_between">
			<div class="company_name_left">
				<i class="icon iconfont icon-tianjiatouziren"></i>
				<span>新增投资人</span>
			</div>
		</div>
		<div class="form flex_between">
			<div class="form_list flex_start">
				<div class="form_list_left">
					投资人类别
				</div>
				<div class="form_list_right">
					<!-- 投资人类别 -->
					<el-cascader :options="investerType" v-model="investor_class_list" @change="chooseInvestorType" :show-all-levels="false"  :disabled="true" >
  					</el-cascader>
					<!-- <el-input placeholder="请输入内容" v-model="name" clearable>
					</el-input> -->
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					投资人名称
				</div>
				<div class="form_list_right">
					<div class="" v-if="investor_class_list[0] != 2">
						<el-select v-model="company_id" @change="chooseInvestorName" filterable clearable placeholder="请选择投资人名称">
							<el-option v-for="item in manage_company_list" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
					<div class="" v-else>
						<el-input placeholder="请输入内容" v-model="name" clearable>
						</el-input>
					</div>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2">
				<div class="form_list_left">
					投资人性质
				</div>
				<div class="form_list_right">
					<el-select v-model="partner_type" clearable placeholder="请选择">
						<el-option v-for="item in investerCharacter" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					是否为主投资人
				</div>
				<div class="form_list_right">
					<div class="">
						<el-select v-model="is_main" clearable placeholder="请选择">
							<el-option v-for="item in is_master_investor_list" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					投资人身份
				</div>
				<div class="form_list_right">
					<el-select v-model="investor_type" clearable placeholder="请选择">
						<el-option v-for="item in investerIdentity" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					投资人手机号
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="cell" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					身份到期日
				</div>
				<div class="form_list_right">
					<el-date-picker v-model="document_expire" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd" :editable='false'>
					</el-date-picker>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					邮箱
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="email" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					籍贯
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="place" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					有效证件类型
				</div>
				<div class="form_list_right">
					<el-select v-model="document_type" clearable placeholder="请选择">
						<el-option v-for="item in identityList" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					性别
				</div>
				<div class="form_list_right">
					<el-select v-model="sex" clearable placeholder="请选择">
						<el-option v-for="item in sexList" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					有效证件号码
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="document_no" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					生日
				</div>
				<div class="form_list_right">
					<el-date-picker v-model="birthday" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd" :editable='false'>
					</el-date-picker>
				</div>
			</div>
			
			
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					其他证件说明
				</div>
				<div class="form_list_right">
					<el-input placeholder="请填写国家名称" v-model="otherdoc_info" clearable>
					</el-input>
				</div>
			</div>
			
			
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					投资人所属公司
				</div>
				<div class="form_list_right">
					<el-input placeholder="请填写公司名称" v-model="company" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					收件详细地址
				</div>
				<div class="form_list_right">
					<el-input placeholder="请填写详细地址" v-model="address" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					证件有效期
				</div>
				<div class="form_list_right">
					<el-date-picker v-model="document_time" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd" :editable='false'>
					</el-date-picker>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					关联人
				</div>
				<div class="form_list_right">
					<el-select v-model="relate_investor" filterable placeholder="" multiple collapse-tags>
						<el-option v-for="item in allInvestorList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					IR
				</div>
				<div class="form_list_right">
					<el-select v-model="ir" filterable clearable placeholder="请选择">
						<el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					客户经理
				</div>
				<div class="form_list_right">
					<el-select v-model="manager" filterable clearable placeholder="请选择">
						<el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					创建人
				</div>
				<div class="form_list_right">
					<el-select v-model="creater" filterable clearable placeholder="请选择">
						<el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					是否开启客户端账号
				</div>
				<div class="form_list_right flex_start">
					<div @click="changeHideStatus(1)" class="radio_btn cursor" :class="[is_open_status == 1?'radio_btn_active':'']">
						是
					</div>
					<div @click="changeHideStatus(2)" class="radio_btn cursor" :class="[is_open_status == 2?'radio_btn_active':'']">
						否
					</div>
				</div>
			</div>
			<div class="form_list flex_start" v-if="investor_class == 2&&partner_type != 2">
				<div class="form_list_left">
					登录手机号
				</div>
				<div class="form_list_right">
					<el-input placeholder="请填写登录手机号" v-model="phone" clearable>
					</el-input>
				</div>
			</div>
		</div>
		<div class="line"> </div>
		<div class="form_title">
			账户信息
		</div>
		<div class="form flex_between">
			<div class="form_list flex_start">
				<div class="form_list_left">
					户名
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="account_name" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					银行账号
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="account_bankno" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					开户银行
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="account_bank" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					支行
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="subbranch" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					支行省份
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="subbranch_prevince" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					支行市
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="subbranch_city" clearable>
					</el-input>
				</div>
			</div>
		</div>
		<!--表单按钮模块-->
		<div class="form_submit flex_center">
			<button @click="cancel">取消</button>
			<button @click="addInvesterManageInfo">保存</button>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getInvesterManageListApi,getUserlistApi,addInvesterManageInfoApi,getInvestorInvestGroupFundSelectListApi } from '../../config/api'
	
	export default {
		name: 'investerManageAdd',
		inject: ['reload'],
		data() {
			return {
				// 表单字段开始
				name: '',
				cell: '',
				status: 1,
				email: '',
				company: '',
				phone: '',
				is_main: '',
				document_type: '',
				document_no: '',
				document_expire: '',//身份到期日
				document_time: '',//证件有效期
				otherdoc_info: '',
				relate_investor: [],
				investor_type: '',
				partner_type: '',
				investor_class: 2,
				investor_class_list: [2],
				ir: '',
				manager: '',
				creater: '',
				place: '',
				sex: '',
				birthday: '',
				address: '',
				is_open_status: 2,
				
				account_name: '',
				account_bankno: '',
				account_bank: '',
				subbranch: '',
				subbranch_prevince: '',
				subbranch_city: '',
				company_id: '',//如果investor_class == 2(管理公司)或者母基金、自管基金、子基金类型时 记录的id
				// 表单字段结束
				manage_company_list: [ ],
				is_master_investor_list: [
					{
						id: 1,
						name: '是'
					},
					{
						id: 0,
						name: '否'
					},
				],
				allUserList: [],
				allInvestorList:[],
				sexList: [
					{
						label: '男',
						value: 1
					},
					{
						label: '女',
						value: 2
					}
				],
				investerIdentity: [
					{
						value: 1,
						label: '普通'
					},
					{
						value: 2,
						label: '专业'
					}
				],
				investerCharacter: [
					{
						value: 1,
						label: '自然人'
					},
					{
						value: 2,
						label: '机构'
					},
				],
				// 投资人类别
				investerType: [
					{
						value: 0,
						label: '基金',
						children: [
							{
								value: 3,
								label: '母基金'
							},
							{
								value: 5,
								label: '自管基金'
							},
							{
								value: 4,
								label: '子基金'
							}
						]
					},
					{
						value: 1,
						label: '管理公司'
					},
					{
						value: 2,
						label: '一般投资人'
					}
				],
				identityList: [
					{
						value: 1,
						label: '身份证'
					},
					{
						value: 2,
						label: '护照'
					},
					{
						value: 3,
						label: '其他国家证件'
					},
				],
			}
		},
		created() {
			this.getAllUserList();
			this.getReationPersonList();
		},
		mounted() {},
		components: {},
		methods: {
			addInvesterManageInfo(){
				var relate_investor = this.relate_investor.join(',');
				if (this.partner_type == 2) {
					this.clearData()
				}
				var info = {
					company_id: this.company_id,
					name: this.name,
					cell: this.cell,
					is_main: this.is_main,
					email: this.email,
					company: this.company,
					phone: this.phone,
					document_type: this.document_type,
					document_no: this.document_no,
					document_expire: this.document_expire,
					document_time: this.document_time,
					otherdoc_info: this.otherdoc_info,
					relate_investor: relate_investor,
					investor_type: this.investor_type,
					partner_type: this.partner_type,
					investor_class: this.investor_class,
					ir: this.ir,
					manager: this.manager,
					creater: this.creater,
					place: this.place,
					sex: this.sex,
					birthday: this.birthday,
					address: this.address,
					open_status: this.is_open_status,
					
					account_name: this.account_name,
					account_bankno: this.account_bankno,
					account_bank: this.account_bank,
					subbranch: this.subbranch,
					subbranch_prevince: this.subbranch_prevince,
					subbranch_city: this.subbranch_city,
				}
				if (this.investor_class == 2) {
					if (this.partner_type && this.name) {
						var is_true_phone = this.checkPhone(this.cell)
						if (this.partner_type == 1) {
							if (!is_true_phone) {
								dialogConfirm("info", '手机号格式错误');
								return
							} 
						}
						
						addInvesterManageInfoApi({
							info: info
						}).then(res => {
							// console.log(res);
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								this.$router.go(-1);
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					} else{
						if (!this.name) {
							dialogConfirm("info", '投资人名称不能为空');
						} else{
							dialogConfirm("info", '投资人性质不能为空');
						}
					}
				} else {
					if (this.company_id) {
						addInvesterManageInfoApi({
							info: info
						}).then(res => {
							// console.log(res);
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.message);
								this.$router.go(-1);
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					} else{
						dialogConfirm("info", '投资人名称不能为空');
					}
				}
			},
			// 获取所有人员列表
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					// console.log(res);
					if(res.data.msgNo == 1) {
						this.allUserList = res.data.payload.data;
						this.creater = parseInt( window.localStorage.getItem('htcx_uid')) ;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			changeHideStatus(e) {
				if(e == 1) {
					this.is_open_status = 1;
				} else {
					this.is_open_status = 2;
				}
			},
			chooseInvestorType(e){
				if (e.length) {
					var investor_class = e[e.length - 1];
					this.investor_class = investor_class;
					if (investor_class == 1) {
						var item = 1;
					} else if (investor_class == 3) {
						var item = 2;
					} else if (investor_class == 4) {
						var item = 4;
					} else if (investor_class == 5) {
						var item = 3;
					}
					if (e[e.length - 1] != 2) {
						this.getFundSelectList(item)
					}
				} else{
					this.investor_class = '';
				}
				this.clearData()
			},
			getFundSelectList(e){
				// console.log(e);
				getInvestorInvestGroupFundSelectListApi({
					type: e
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.manage_company_list = res.data.payload;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			chooseInvestorName(e){
				if (e) {
					var item = this.getSelectedItem(e);
					this.name = item.name;
				}
			},
			getSelectedItem(value){
				var item = this.manage_company_list.find((item) => {
					return item.id == value
				})
				return item
			},
			clearData(){
				this.relate_investor = [];
				this.document_time = '';
				this.company = '';
				this.otherdoc_info = '';
				this.birthday = '';
				this.document_no = '';
				this.sex = '';
				this.document_type = '';
				this.place = '';
				this.email = '';
				this.document_expire = '';
				this.cell = '';
				this.investor_type = '';
				this.is_main = '';
			},
			// 返回上一个列表页面
			cancel() {
				//this.reload()
				this.$router.go(-1);
			},
			// 手机号验证
			checkPhone(phone){ 
			    if((/^1[23456789]\d{9}$/.test(phone))){ 
			        return true; 
			    } else if(phone == ''){
			    		return true
			    } else {
			    		return false
			    }
			},
			getReationPersonList(){
				getInvesterManageListApi({
					page: 1,
					pageSize: 10000,
					name:'',
					ir: '',
					manager: ''
				}).then(res => {
					if(res.data.msgNo == 1) {
						// console.log(res.data.payload);
						this.allInvestorList = res.data.payload.data;
						// this.tableData = res.data.payload.data;
						// this.total = res.data.payload.total;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			}
		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 30px 30px 30px;
		font-size: 14px;
	}
	
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	
	.form {
		flex-wrap: wrap;
		padding: 30px 0;
		color: #6F7071;
	}
	.form_title{
		font-size: 16px;
		color: #333;
	}
	.form_list {
		width: 40%;
		display: inline-flex;
		margin-bottom: 19px;
	}
	
	.form_list_left {
		width: 140px;
		color: #6F7071;
	}
	
	.form_list_right {
		width: 260px;
		height: 40px;
	}
	
	.radio_btn {
		width: 73px;
		height: 40px;
		margin-right: 38px;
		line-height: 40px;
		text-align: center;
		border-radius: 20px;
		background: #F3F6FB;
	}
	
	.radio_btn_active {
		color: white;
		background: #6292F4 !important;
	}
	
	.form_submit button {
		width: 94px;
		height: 36px;
		color: #6292F4;
		font-size: 14px;
		margin-right: 26px;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.form_submit button:nth-child(2) {
		color: white;
		background: #6292F4;
	}
	.line {
        width: 100%;
        height: 1px;
        background: #F4F4F4;
        margin: 0px 0 40px 0;
    }
</style>


